nav{
    display: block;
    position:fixed;
    margin-top:30px;
    background-color :rgba(0, 0, 0, 0);
    padding:10px;
    width:50px;
    height:200px;
    margin-top:100px;
    display:flex;
    flex-direction: column;
    gap:0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a{
    background: transparent;
    padding:0.9rem;
    border-radius: 50%;
    margin:auto;
    color:var(--color-light);
    font-size:1.1rem;
}

nav a:hover{
    background: rgba(255, 255, 255, 0.3);
}

.icons{
    width:30px;
    height: 30px;
}