.songs{

    margin-top:40px;
}
.song{
    margin-bottom:50px;
}
.eg-container{
    
    display:grid;
    margin:auto;
    grid-template-columns: 1fr 1fr 1fr;
    width:50%;
    margin-left:-15px;
    gap:4rem;
}

.imaage{
    width:200px;
    aspect-ratio: 1/1;
    border-radius:10%;
}

.container{
    display: grid;
    justify-content: center;
    text-align: center;
}

.line{
    border-style: solid;
    color:aqua;
    width:700px;
}

.Player{
    position:fixed;
    width:10px;
}

.p{
    position:fixed;
    bottom:0px;
    left:90vh;
}

@media screen and (max-width:1024px){
    .song{
        width:10px;
    }
  
  }
  @media screen and (max-width:600px){
    .container{
        width:var(--container-width-s,m);
    }
  
    section>h2{
        margin-top:2rem;
    }
  }